.ToolBox {
  flex-basis: 100%;
  background: var(--primary-dark-7);
}
.Toolbar {
  height: 100%;
  width: 64px;
  background: var(--primary-dark-9);
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--primary-light-9);
  .icon {
    font-size: 40px;
    margin: 8px 0;
  }
}