
$main-primary-3: #555;
$main-primary-1: lighten( $main-primary-3, 50% );
$main-primary-2: lighten( $main-primary-3, 25% );
$main-primary-4: darken( $main-primary-3, 25% );
$main-primary-5: darken( $main-primary-3, 50% );

$main-secondary-3: #aaa;
$main-secondary-1: lighten( $main-secondary-3, 50% );
$main-secondary-2: lighten( $main-secondary-3, 25% );
$main-secondary-4: darken( $main-secondary-3, 25% );
$main-secondary-5: darken( $main-secondary-3, 50% );

$font-small-1: 22pt;
$viewer-primary-3: #444;
$viewer-primary-1: lighten( $viewer-primary-3, 50% );
$viewer-primary-2: lighten( $viewer-primary-3, 25% );
$viewer-primary-4: darken( $viewer-primary-3, 25% );
$viewer-primary-5: darken( $viewer-primary-3, 50% );

$viewer-secondary-3: #3F59A8;
$viewer-secondary-1: lighten( $viewer-secondary-3, 50% );
$viewer-secondary-2: lighten( $viewer-secondary-3, 25% );
$viewer-secondary-4: darken( $viewer-secondary-3, 25% );
$viewer-secondary-5: darken( $viewer-secondary-3, 50% );

$font-small-1: 22pt;
$font-small-2: 20pt;
$font-small-3: 18pt;
$font-small-4: 16pt;
$font-small-5: 14pt;
$font-small-6: 12pt;
$font-small-7: 10pt;

html{
  --primary-hue: 225;
  --primary-saturation: 63%;
  --primary-lightness: 41%;
  --primary: hsl(var(--primary-hue), var(--primary-saturation), var(--primary-lightness));
  --primary-dark: hsl(var(--primary-hue), var(--primary-saturation), 5%);
  --primary-dark-9: hsla(var(--primary-hue), var(--primary-saturation), 5%, .9);
  --primary-dark-7: hsla(var(--primary-hue), var(--primary-saturation), 5%, .7);
  --primary-light: hsl(var(--primary-hue), var(--primary-saturation), 90%);
  --primary-light-9: hsla(var(--primary-hue), var(--primary-saturation), 90%, .9);

